@import 'ScssHelpers/helpers-all.scss';

.list {
	visibility: visible;
	background-color: $black_4;
	color: $white;

	@include bp($point_2, min) {
		padding: 7.8rem var(--gl-indent);
		overflow: hidden;
	}

	@include bp($point_3) {
		padding: 4.2rem var(--gl-indent) 0;
	}
}

.list__cols {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
}

.list__col {
	&:nth-child(1) {
		position: relative;

		@include bp($point_2, min) {
			display: flex;
			// width: 64%;
			width: 58%;
			margin: -7.8rem 0;
			min-height: 48.6rem;
		}

		@include bp($point_3) {
			margin: 0 -1rem;
			width: calc(100% + 2rem);
			min-height: 35.4rem;
			order: 2;
		}
	}

	&:nth-child(2) {
		@include bp($point_2, min) {
			// width: 36%;
			width: 42%;
		}

		@include bp($point_3) {
			width: 100%;
			margin-bottom: 8rem;
		}
	}
}

.list__main {
	position: absolute;
	top: 0;
	height: 100%;

	@include bp($point_2, min) {
		left: 0;

		width: 100%;
	}

	@include bp($point_3) {
		padding: 0 var(--gl-indent);
		left: calc(#{var(--gl-indent)} * -1);
		width: calc(100% + #{var(--gl-indent)} * 2);

		overflow: hidden;
	}
}

.list__main_shadow {
	position: absolute;
	top: 50%;
	left: 0;

	filter: blur(0.4rem);

	will-change: opacity;

	@include bp($point_2, min) {
		width: 60rem;
		transform: translate(-23%, -55%);
	}

	@include bp($point_3) {
		width: 40rem;
		transform: translate(-18%, -55%);
	}
}

.list__main_shadow_img {
	@include contain_img;
}

.list__main_list {
	position: absolute;
	z-index: 2;

	will-change: transform, opacity;

	&:nth-child(n + 2) {
		opacity: 0;
		transform: translateX(-5rem);
	}
}

.list__main_item {
	display: flex;
	align-items: center;

	line-height: 1.24;
	font-family: $sfd;

	-webkit-text-stroke: 1px white;
	-webkit-text-fill-color: transparent;

	transform-origin: 0% 50%;

	&:nth-child(5) {
		-webkit-text-stroke: 1px transparent;
		-webkit-text-fill-color: $yellow;
	}

	@include bp($point_2, min) {
		font-size: 5.3rem;
	}

	@include bp($point_3) {
		padding-left: 2rem;
		margin-bottom: 1rem;

		font-size: 2.9rem;
	}
}

.list__main_item_avatar {
	border: 1px solid $white;
	border-radius: 50%;

	overflow: hidden;

	@include bp($point_2, min) {
		width: 3.6rem;
		height: 3.6rem;
		margin-right: 2.4rem;
	}

	@include bp($point_3) {
		width: 2.4rem;
		height: 2.4rem;
		margin-left: -3.3rem;
		margin-right: 0.6rem;
	}
}

.list__main_item_pic {
	display: block;
	width: 100%;
	height: 100%;
}

.list__main_item_img {
	@include cover_img;
}

.list_card {
	display: flex;
	width: 40.8rem;
	max-width: 100%;
	padding: 0.7rem 0.7rem 1.6rem;

	color: $black_3;

	background-color: $white;
	border: 0.2rem solid rgba(#091e42, 0.54);

	opacity: 0;

	@include bp($point_2, min) {
		margin: auto 0;
	}
}

.list_card__col {
	&:nth-child(1) {
		@include bp($point_2, min) {
			margin-right: 0.4rem;
			width: 9.8rem;
		}

		@include bp($point_3) {
			width: 7.8rem;
			margin-right: 0.6rem;
		}
	}

	&:nth-child(2) {
		flex-grow: 1;
		padding-top: 0.4rem;
	}
}

.list_card__avatar {
	position: relative;

	display: block;
	max-width: 9.4rem;
	width: 100%;

	&:before {
		content: '';
		display: block;
		padding-bottom: 100%;
	}
}

.list_card__avatar_img {
	position: absolute;
	top: 0;
	left: 0;

	@include cover_img;
}

.list_card__location {
	display: flex;
	align-items: center;
	margin-bottom: 0.5rem;

	@include bp($point_3) {
		margin-right: -0.4rem;
	}
}

.list_card__location_icon {
	@include bp($point_2, min) {
		width: 0.9rem;
		height: 1.1rem;
		margin-right: 0.5rem;
	}

	@include bp($point_3) {
		width: 0.7rem;
		height: 0.9rem;
		margin-right: 0.4rem;
	}
}

.list_card__location_title {
	font-family: $raleway;
	font-weight: 600;

	@include bp($point_2, min) {
		font-size: 1rem;
	}

	@include bp($point_3) {
		font-size: 0.83rem;
	}
}

.list_card__social {
	display: flex;

	@include bp($point_2, min) {
		margin: 0 -0.6rem -0.6rem 0.4rem;
	}

	@include bp($point_3) {
		margin: 0 -0.4rem -0.4rem 0;
	}
}

.list_card__social_item {
	width: 25%;

	@include bp($point_2, min) {
		padding: 0 0.6rem 0.6rem 0;
	}

	@include bp($point_3) {
		padding: 0 0.4rem 0.4rem 0;
	}
}

.list_card__social_link {
	display: flex;

	color: $white;

	border-radius: 50%;
	background-color: #555555;

	transition: background-color 0.3s;

	@include bp($point_2, min) {
		width: 2rem;
		height: 2rem;
		padding: 0.5rem;
	}

	@include bp($point_3) {
		width: 1.6rem;
		height: 1.6rem;
		padding: 0.4rem;
	}

	&:hover,
	&:focus {
		color: $black;
	}
}

.list_card__head {
	margin-bottom: 0.8rem;

	border-bottom: 1.6px solid #808080;

	@include bp($point_2, min) {
		padding-bottom: 0.9rem;
	}

	@include bp($point_3) {
		padding-bottom: 0.6rem;
	}
}

.list_card__title {
	margin-bottom: 0.4rem;

	font-weight: 400;
	line-height: 1;

	@include bp($point_2, min) {
		font-size: 2.5rem;
	}

	@include bp($point_3) {
		font-size: 2.1rem;
	}
}

.list_card__job {
	font-family: $raleway;
	color: #4a4a4a;

	@include bp($point_2, min) {
		font-size: 1.1rem;
	}

	@include bp($point_3) {
		font-size: 0.9rem;
	}
}

.list_card__body {
}

.list_card__body_title {
	margin-bottom: 1rem;

	font-style: italic;
	font-family: $raleway;
	font-weight: 500;
	text-align: center;
	text-transform: uppercase;

	@include bp($point_2, min) {
		font-size: 1.1rem;
	}

	@include bp($point_3) {
		font-size: 0.9rem;
	}
}

.list_card__body_list {
	margin: 0 auto;

	@include bp($point_2, min) {
		max-width: 23rem;
	}

	@include bp($point_3) {
		max-width: 80%;
	}
}

.list_card__body_list_in {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -1.2rem -1.2rem 0;
}

.list_card__body_item {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 33.33%;
	padding: 0 1.2rem 1.2rem 0;
}

.list_card__body_pic {
	display: block;
	max-width: 100%;
}

.list_card__body_img {
	display: block;
	height: 4.2rem;
	width: 4.4rem;
	max-width: 100%;
	object-fit: contain;

	@include bp($point_3) {
		height: 3.2rem;
	}
}

.list__title_wrap {
	position: relative;

	@include bp($point_3) {
		min-height: 15.6rem;
	}

	&:before {
		content: '';
		position: absolute;

		width: 1.5rem;
		height: 1.5rem;

		border-radius: 50%;
		background-color: currentColor;

		@include bp($point_2, min) {
			top: -1.5rem;
			left: -0.8rem;
		}

		@include bp($point_3) {
			top: -1.6rem;
			left: -1.2rem;
		}
	}
}

.list__title {
	position: relative;

	font-weight: 400;
	line-height: 1;

	will-change: opacity;

	&:last-child {
		@include bp($point_2, min) {
			display: flex;
			align-items: center;

			font-size: 9.6rem;
		}
	}

	&:nth-child(n + 2) {
		position: absolute;
		top: 0;
		left: 0;

		opacity: 0;
	}

	@include bp($point_2, min) {
		max-width: 46rem;
		min-height: 29.2rem;

		font-size: 7.3rem;
	}

	@include bp($point_3) {
		font-size: 5.2rem;
	}

	strong {
		font-weight: 400;
		color: $yellow;
	}
}

.list__title_in,
.list__title_in h2 {
	font-weight: 400;
}
